<h1 id="python-object-oriented-programming" tabindex="-1"><a class="header-anchor" href="#python-object-oriented-programming" aria-hidden="true">#</a> Python Object-Oriented Programming</h1>
<p>So far in the course we have primarily used <em>imperative programming</em> (a program consists of a sequence of statements to be executed), but we have also used a bit <em>object-oriented programming</em> since each value in Python actually is an object. In this lecture we take a closer look at what an object is and how we can describe them using classes.</p>
